/* Collapse Styles */
.wp-block-ekiline-collection-ekiline-collapse .item-title {
	border-bottom: var(--ebc-borde-titulo);
}

.editor-collapse-route:not(.has-anchor) {
	background-color: #000;
	color: #fff;
}

.editor-collapse-route.has-anchor {
	background-color: #efefef;
}

/* Popover(s) Styles */
.wp-block-ekiline-collection-ekiline-popovers {
	border: 2px solid #000;
	background-color: #ffff00;
}

.wp-block-ekiline-collection-ekiline-popovers .item-title {
	border-bottom: var(--ebc-borde-titulo);
}

.editor-popovers-route:not(.has-anchor) {
	background-color: #000;
	color: #fff;
}

.editor-popovers-route.has-anchor {
	background-color: #efefef;
}

/* Progress */
.wp-block-ekiline-collection-ekiline-progress.has-child-selected {
	border: 1px solid #000000;
}

.wp-block-ekiline-collection-ekiline-progress .block-editor-inner-blocks {
	background-color: #e9ecef;
	height: 100%;
}

.wp-block-ekiline-collection-ekiline-progress .block-editor-block-list__layout {
	display: flex;
	justify-content: flex-start;
	align-content: stretch !important;
	height: 100%;
	overflow: hidden;
}

.wp-block-ekiline-collection-ekiline-progress
	.block-editor-block-list__layout
	> .wp-block {
	border-right: 1px solid #c9c9c9;
	background-color: rgba(173, 174, 175, 0.638);
	margin: 0px !important;
	max-width: 100% !important;
}

.wp-block-ekiline-collection-ekiline-progress
	.block-editor-block-list__layout
	> .wp-block
	> .wp-block.item-progress {
	// margin: 0px;
	width: 100% !important;
	max-width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}
.wp-block-ekiline-collection-ekiline-progress
	.block-editor-block-list__layout
	> .wp-block
	> .wp-block.item-progress.is-selected {
	border: 1px solid #000;
}

.wp-block-ekiline-collection-ekiline-progress
	.block-editor-block-list__layout
	> .wp-block
	> .wp-block.item-progress
	* {
	text-align: center;
	margin: 0;
}

/* toast */
.wp-block-ekiline-collection-ekiline-toast .toast-item {
	border: 1px solid #c8c8c8;
}
.wp-block-ekiline-collection-ekiline-toast .toast-item .item-title {
	border-bottom: var(--ebc-borde-titulo);
  padding-bottom: 1rem;
}

/* tabs */
.wp-block-ekiline-collection-ekiline-tabs.tabs-wrapper.wp-block,
.tabs-wrapper.wp-block {
	margin-top: initial;
}

.tabs-wrapper
	.wp-block
	.wp-block:not(.tabs-navbar, .tabs-container, .tab-content) {
	max-width: 100% !important;
	margin-top: 0px !important;
	margin-bottom: 5px !important;
}

.tabs-navbar .wp-block:not(.tab-link) {
	margin-right: 1px;
}

.tabs-navbar .block-editor-inner-blocks .block-editor-block-list__layout {
	display: flex;
	margin-bottom: 2px !important;
}

.tabs-navbar .wp-block {
	margin-left: 0px;
	margin-right: 0px;
}

.editor-styles-wrapper .tab-link {
	border: 1px solid #cfcfcf;
	background-color: #ffffff;
}

.editor-styles-wrapper .tab-content {
	border-bottom: var(--ebc-borde-titulo);
}

/** Modal **/
.editor-modal-header {
	border-bottom: var(--ebc-borde-titulo);
}
.editor-modal-footer {
	border-top: var(--ebc-borde-titulo);
}
.editor-modal-route:not(.has-anchor) {
	background-color: #000;
	color: #fff;
}
.editor-modal-route.has-anchor {
	background-color: #efefef;
}

/* Carousel */

.wp-block-ekiline-collection-ekiline-carousel,
.wp-block-ekiline-collection-ekiline-carousel-extra {
	padding: 1px;
	background-color: rgba(0, 0, 0, 0.2);
}

/* Carousel: bootstrap */
.visually-hidden,
.visually-hidden-focusable:not(:focus):not(:focus-within) {
	position: absolute !important;
	width: 1px !important;
	height: 1px !important;
	padding: 0 !important;
	margin: -1px !important;
	overflow: hidden !important;
	clip: rect(0, 0, 0, 0) !important;
	white-space: nowrap !important;
	border: 0 !important;
}

.carousel {
	position: relative;
}

.carousel.pointer-event {
	touch-action: pan-y;
}

.carousel-inner {
	position: relative;
	width: 100%;
	overflow: hidden;
}
.carousel-inner::after {
	display: block;
	clear: both;
	content: "";
}

.carousel-item {
	position: relative;
	display: none;
	float: left;
	width: 100%;
	margin-right: -100%;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
	transition: transform 0.6s ease-in-out;
}
@media (prefers-reduced-motion: reduce) {
	.carousel-item {
		transition: none;
	}
}

.carousel-item.active,
.carousel-item-next,
.carousel-item-prev {
	display: block;
}

/* rtl:begin:ignore */
.carousel-item-next:not(.carousel-item-start),
.active.carousel-item-end {
	transform: translateX(100%);
}

.carousel-item-prev:not(.carousel-item-end),
.active.carousel-item-start {
	transform: translateX(-100%);
}

/* rtl:end:ignore */
.carousel-fade .carousel-item {
	opacity: 0;
	transition-property: opacity;
	transform: none;
}
.carousel-fade .carousel-item.active,
.carousel-fade .carousel-item-next.carousel-item-start,
.carousel-fade .carousel-item-prev.carousel-item-end {
	z-index: 1;
	opacity: 1;
}
.carousel-fade .active.carousel-item-start,
.carousel-fade .active.carousel-item-end {
	z-index: 0;
	opacity: 0;
	transition: opacity 0s 0.6s;
}
@media (prefers-reduced-motion: reduce) {
	.carousel-fade .active.carousel-item-start,
	.carousel-fade .active.carousel-item-end {
		transition: none;
	}
}

.carousel-control-prev,
.carousel-control-next {
	position: absolute;
	top: 0;
	bottom: 0;
	z-index: 1;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 15%;
	padding: 0;
	color: #fff;
	text-align: center;
	background: none;
	border: 0;
	opacity: 0.5;
	transition: opacity 0.15s ease;
}
@media (prefers-reduced-motion: reduce) {
	.carousel-control-prev,
	.carousel-control-next {
		transition: none;
	}
}
.carousel-control-prev:hover,
.carousel-control-prev:focus,
.carousel-control-next:hover,
.carousel-control-next:focus {
	color: #fff;
	text-decoration: none;
	outline: 0;
	opacity: 0.9;
}

.carousel-control-prev {
	left: 0;
}

.carousel-control-next {
	right: 0;
}

.carousel-control-prev-icon,
.carousel-control-next-icon {
	display: inline-block;
	width: 2rem;
	height: 2rem;
	background-repeat: no-repeat;
	background-position: 50%;
	background-size: 100% 100%;
}

.carousel-control-prev-icon {
	background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23fff'%3e%3cpath d='M11.354 1.646a.5.5 0 0 1 0 .708L5.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z'/%3e%3c/svg%3e");
}

.carousel-control-next-icon {
	background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23fff'%3e%3cpath d='M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e");
}

.carousel-indicators {
	position: absolute;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 2;
	display: flex;
	justify-content: center;
	padding: 0;
	margin-right: 15%;
	margin-bottom: 1rem;
	margin-left: 15%;
	list-style: none;
}
.carousel-indicators [data-bs-target] {
	box-sizing: content-box;
	flex: 0 1 auto;
	width: 30px;
	height: 3px;
	padding: 0;
	margin-right: 3px;
	margin-left: 3px;
	text-indent: -999px;
	cursor: pointer;
	background-color: #fff;
	background-clip: padding-box;
	border: 0;
	border-top: 10px solid transparent;
	border-bottom: 10px solid transparent;
	opacity: 0.5;
	transition: opacity 0.6s ease;
}
@media (prefers-reduced-motion: reduce) {
	.carousel-indicators [data-bs-target] {
		transition: none;
	}
}
.carousel-indicators .active {
	opacity: 1;
}

.carousel-caption {
	position: absolute;
	right: 15%;
	bottom: 1.25rem;
	left: 15%;
	padding-top: 1.25rem;
	padding-bottom: 1.25rem;
	color: #fff;
	text-align: center;
}

.carousel-dark .carousel-control-prev-icon,
.carousel-dark .carousel-control-next-icon {
	filter: invert(1) grayscale(100);
}
.carousel-dark .carousel-indicators [data-bs-target] {
	background-color: #000;
}
.carousel-dark .carousel-caption {
	color: #000;
}

/* Carousel: ekiline preview */
.carousel,
.carousel-item {
	min-height: 50px;
}

.carousel .carousel-item {
	overflow: hidden;
}

@media only screen and (min-width: 768px) {
	.carousel-multiple .carousel-inner {
		display: flex;
	}

	.carousel-multiple[class*="x"] .carousel-item {
		margin-right: 0px;
	}

	.carousel-multiple.x2 .carousel-item:nth-child(-n + 2) {
		display: block !important;
		width: calc(100% / 2);
	}

	.carousel-multiple.x3 .carousel-item:nth-child(-n + 3) {
		display: block !important;
		width: calc(100% / 3);
	}

	.carousel-multiple.x4 .carousel-item:nth-child(-n + 4) {
		display: block !important;
		width: calc(100% / 4);
	}
}

/* Carousel: ekiline side controls */
@keyframes animation_fadein_bottom {
	from {
		opacity: 0;
		transform: translateY(100%);
	}
	to {
		opacity: 1;
	}
}
@keyframes animation_grow_right {
	0% {
		width: 0%;
	}
	100% {
		width: 90%;
	}
}
.has-text-indicators .carousel-caption:not(.carousel-text-indicators) {
	top: 1.25rem;
	text-align: left;
	animation-duration: 1s;
	animation-fill-mode: both;
	animation-name: animation_fadein_bottom;
}
@media screen and (min-width: 768px) {
	.has-text-indicators .carousel-caption:not(.carousel-text-indicators) {
		width: 40%;
		top: calc(100% / 4);
	}
}
.has-text-indicators .carousel-indicators:first-child {
	justify-content: start;
}
.has-text-indicators .carousel-text-indicators.carousel-caption {
	left: 70%;
	width: 25%;
	top: 1.25rem;
	margin-bottom: 0px;
	right: auto;
	padding: 0px;
	text-align: left;
	flex-direction: column;
	justify-content: center;
	z-index: 1;
	list-style: none;
	display: flex;
}
.has-text-indicators .carousel-text-indicators [data-bs-target] {
	padding-top: 15px;
	padding-bottom: 15px;
	transition: 0.6s ease;
	opacity: 0.5;
}
.has-text-indicators .carousel-text-indicators .active {
	opacity: 1;
}
.has-text-indicators .carousel-text-indicators .active::after {
	content: "";
	height: 3px;
	display: block;
	background-color: rgba(var(--bs-light-rgb), 0.5);
	animation: 4s animation_grow_right;
	margin-top: 10px;
	width: 90%;
}

/** offcanvas **/
.editor-offcanvas-header {
	border-bottom: var(--ebc-borde-titulo);
	padding-top: 10px;
	padding-bottom: 10px;
}
.editor-offcanvas-body {
	padding-top: 10px;
	padding-bottom: 10px;
}

.editor-offcanvas-route:not(.has-anchor) {
	background-color: #000;
	color: #fff;
}

.editor-offcanvas-route.has-anchor {
	background-color: #efefef;
}

/* Carrusel de bloques */
.editor-styles-wrapper .carousel-content {
	border-bottom: var(--ebc-borde-titulo);
}
